<template>
    <h1>CALCULO DE TIRO (v0.3)</h1>
	
	<div>
		<p>alc: {{ alc }}, step: {{ step }}</p>
		<input v-model="alc" />
	</div>

	<div>
		<button v-on:click="set_alcance_alvo(10000.)">alc: 10000</button>
		<button v-on:click="set_alcance_alvo(20000.)">alc: 20000</button>
		<button v-on:click="set_alcance_alvo(40000.)">alc: 40000</button>
		<button v-on:click="set_alcance_alvo(60000.)">alc: 60000</button>
		<button v-on:click="set_alcance_alvo(80000.)">alc: 80000</button>
	</div>
	
	<div>
		<button v-on:click="set_step(100.)">step 100</button>
		<button v-on:click="set_step(500.)">step 500</button>
		<button v-on:click="set_step(1000.)">step 1000</button>
		<button v-on:click="set_step(10000.)">step 10000</button>
		<button v-on:click="set_alc_incr(-1)">-</button>
		<button v-on:click="set_alc_incr(+1)">+</button>
	</div>
	
	<div>
		<p>alt: {{ alt }}</p>
		<input v-model="alt" />
	</div>

	<button v-on:click="set_atmosisa">set_atmosisa</button>
	
	<div>
		<p>FOG: {{ fog }} (secb: {{secb}})</p>
	</div>
	
	<div>
		<button v-on:click="set_fog('SS-30')">SS30</button>
		<button v-on:click="set_fog('SS-40')">SS40</button>
		<button v-on:click="set_fog('SS-60')">SS60</button>
		<button v-on:click="set_fog('SS-80')">SS80</button>
	</div>
	<div>
		<button v-on:click="set_secbal(0)">SECB: 0</button>
		<button v-on:click="set_secbal(1)">SECB: 1</button>
		<button v-on:click="set_secbal(2)">SECB: 2</button>
	</div>

	<br>
	<button v-on:click="get_tiro">get_tiro</button>
	<button v-on:click="elev+=100">Mais</button>
	<button v-on:click="Bla">OK</button>

	<div v-if="'elev' in dados" class="resposta">
		<h1>RESULTADO:</h1>
		<p>---------------------------------</p>
		<p>FOG..............[]: {{fog}}</p>
		<p>---------------------------------</p>
		<p>elev.........[mils]: {{dados.elev.toFixed(2)}}</p>
		<p>fuze..........[seg]: {{dados.fuze.toFixed(2)}}</p>
		<p>azi..........[mils]: {{dados.azi.toFixed(2)}}</p>
		<p>---------------------------------</p>
		<p>impacto_e.......[m]: {{dados.impacto_e.toFixed(2)}}</p>
		<p>impacto_n.......[m]: {{dados.impacto_n.toFixed(2)}}</p>
		<p>---------------------------------</p>
		<p>alc.............[m]: {{dados.alc.toFixed(2)}}</p>
		<p>dalc............[m]: {{dados.dalc.toFixed(2)}}</p>
		<p>dlat............[m]: {{dados.dlat.toFixed(2)}}</p>
		<p>---------------------------------</p>
		<p>temperatura.[celsius]: {{dados.temperatura.toFixed(2)}}</p>
		<p>pressao........[mBar]: {{dados.pressao.toFixed(2)}}</p>
		<p>---------------------------------</p>
		<p>h_lmu........[m]: {{dados.h_lmu.toFixed(2)}}</p>
		<p>h_met........[m]: {{dados.h_met.toFixed(2)}}</p>
		<p>h_fg.........[m]: {{dados.h_fg.toFixed(2)}}</p>
		<p>h_alvo.......[m]: {{dados.h_alvo.toFixed(2)}}</p>
	</div>
</template>

<script>
import moduleWasm from "../assets/wasm_pong"

export default {
	name: 'Inicio',
	
	async created() {
		const instance = await moduleWasm();
		this.instance = instance
		this.instance.init();
		this.set_fog(this.fog)
    },

    props: {
		msg: String,
	},

	data() {
		return {
			elev: 400,
			alt: 0.,
			alc: 25000.,
			step: 1000.,
			secb: 0,
			fog: 'SS-60',
			message: 'Hello Vue!',
			resposta: '',
			dados: []

		}
	},
	
	methods: {
		Bla() {
			let alc = parseFloat(this.alc)
			let resp = this.instance.hello(alc)
			this.dados = resp
		},
		set_fog(fog) {
			fog = fog.replace('-', '_');
			this.fog = fog;
			this.instance.set_fog(this.fog);
		},
		
		set_atmosisa() {
			let alt = parseFloat(this.alt)
			this.instance.set_atmosisa(alt);
		},

		set_secbal(secb) {
			this.secb = parseInt(secb)
			this.instance.set_secbal(this.secb);
		},
		
		get_tiro() {
			this.instance.get_tiro();
		},

		set_alc_incr(inc) {

			if (inc > 0.) 
				inc = this.step
			else
				inc = -this.step
				
			this.alc = parseFloat(this.alc) + parseFloat(inc)
			this.instance.set_alcance_alvo(this.alc)
		},
		
		set_alcance_alvo(alc) {
			this.alc = parseFloat(alc)
			this.instance.set_alcance_alvo(this.alc)
		},
		
		set_step(step) {

			this.step = parseFloat(step)
		}
		
	}
}


</script>